body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: linear-gradient(0.25turn, #d14c4c, #4f2424, #69393a);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
    font-family: 'Semillir Regular';
    font-style: normal;
    font-weight: normal;
    src: local('Semillir Regular'), url(/WesterosRobots01/static/media/Semillir-mLM5V.0bd60121.woff) format('woff');
    }

    h1 {
        font-family: 'Semillir Regular';
        font-weight: bold;
        color: #c2ad77;

    }
